import storage from './tools/local_storage'

// 
export default {
  //
  userInfo: null,

  //
  clueMemberInfo:null,

  // token
  token: storage.get('accessToken') || '',

  set (key, val) {
    this[key] = val
  },

  get (key) {
    return this[key]
  }
}



