import Vue from 'vue'
import Router from 'vue-router'

import hb from './hb'
import yyh from './yyh'

Vue.use(Router)

export default new Router({
    mode: 'history', //history
    scrollBehavior: () => ({ y: 0 }), // 
    routes: hb.concat(yyh),
})



